%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}
%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}
%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}
.chart-block-container {
  canvas {
    height: 400px !important;
    width: auto !important;
    margin: 0 auto;
  }
}
.chart-block {
  canvas {
    width: auto !important;
  }
  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}
.amcharts-export-canvas {
  position: absolute;
  display: none;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $white;
  .canvas-container {
    transition: opacity 0.5s ease-out;
  }
  &.active {
    display: block;
  }
  &.dropbox .canvas-container {
    opacity: 0.5;
  }
}
.all-chart {
  .flot-chart-container {
    height: 350px;
  }
}
.radial-bar {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  background-color: transparent;
  box-sizing: content-box;
  width: 80px;
  height: 80px;
  font-size: 18px;
  background-clip: content-box;
  &:after {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    content: attr(data-label);
    background-color: $white;
    z-index: 1;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }
  > img {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    z-index: 3;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }
}
.radial-bar {
  &.radial-bar-lg {
    width: 100px;
    height: 100px;
    font-size: 20px;
    &:after {
      @extend %chart-1;
    }
    > img {
      @extend %chart-1;
    }
  }
  &.radial-bar-sm {
    width: 50px;
    height: 50px;
    font-size: 12px;
    &:after {
      @extend %chart-2;
    }
    > img {
      @extend %chart-2;
    }
  }
  &.radial-bar-xs {
    width: 30px;
    height: 30px;
    font-size: 8px;
    &:after {
      @extend %chart-3;
    }
    > img {
      @extend %chart-3;
    }
  }
}
// knob chart css
.knob-chart {
  min-height: 255px;
  .chart-clock-main {
    position: relative;
    width: 200px;
    margin: 45px auto;
    .clock-large {
      position: absolute;
      left: -48px;
      top: -45px;
    }
    .clock-medium {
      position: absolute;
      left: 2px;
      top: 5px;
    }
    .clock-small {
      position: absolute;
      left: 15px;
      top: 55px;
    }
  }
}
// knob chart
.review {
  canvas {
    position: unset !important;
  }
}
.null {
  canvas {
    position: unset !important;
  }
}
.knob-block {
  position: relative;
  input {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }
}
// chartist chart
.ct-series-a {
  .ct-slice-pie,
  .ct-slice-donut-solid,
  .ct-area {
    fill: var(--theme-default);
  }
  .ct-point,
  .ct-line,
  .ct-bar,
  .ct-slice-donut {
    stroke: var(--theme-default);
  }
}
.ct-series-b {
  .ct-point,
  .ct-line,
  .ct-bar,
  .ct-slice-donut {
    stroke: var(--theme-secondary);
  }
  .ct-slice-pie,
  .ct-slice-donut-solid,
  .ct-area {
    fill: var(--theme-secondary);
  }
}
// apex chart css
.apexcharts-canvas {
  .apexcharts-toolbar {
    z-index: 1;
  }
}
/**=====================
    42. Chart CSS Ends
==========================**/