/**=====================
    69. task app CSS Start
==========================**/
.modal-open {
  .datepickers-container {
    z-index: 1075;
  }
}
.taskadd {
  table {
    tbody{
      border-top: 1px solid $light-gray !important;
    }
    tr {
      td {
        border-top: none;
        &:first-child {
          padding-left: 30px;
        }
        &:last-child {
          padding-right: 30px;
        }
        svg {
          width: 18px;
          height: 18px;
          color: $semi-dark;
        }
      }
      &:first-child {
        td {
          padding-top: 30px;
        }
      }
      &:last-child {
        td {
          padding-bottom: 30px;
        }
      }
    }
  }
}
// responsive
@media only screen and (max-width: 1660px) {
  .taskadd {
    table {
      tr {
        td {
          &:first-child {
            min-width: 160px;
          }
          &:nth-child(3) {
            min-width: 70px;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 991px) {
  .taskadd {
    table {
      tr {
        td {
          &:first-child {
            padding-left: 20px;
          }
        }
        &:first-child {
          td {
            padding-top: 20px;
          }
        }
        &:last-child {
          td {
            padding-bottom: 20px;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 767px) {
  .taskadd {
    table {
      tr {
        td {
          &:nth-child(2) {
            min-width: 745px;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 575px) {
  .taskadd {
    table {
      tr {
        td {
          &:first-child {
            padding-left: 15px;
          }
        }
        &:first-child {
          td {
            padding-top: 15px;
          }
        }
        &:last-child {
          td {
            padding-bottom: 15px;
          }
        }
      }
    }
  }
}
/**=====================
    69. task app CSS Ends
==========================**/